@font-face {
    font-family: 'SFProDisplay';
    src: local('SFProDisplay'), url(./fonts/SFProDisplay/SFPRODISPLAYREGULAR.OTF) format('opentype');
    font-weight: normal;
}
@font-face {
    font-family: 'SFProDisplay';
    src: local('SFProDisplay'), url(./fonts/SFProDisplay/SFPRODISPLAYBOLD.OTF) format('opentype');
    font-weight: bold;
}
@font-face {
  font-family: 'SFProText';
  src: local('SFProText'), url(./fonts/SFProText/SF-Pro-Text-Regular.otf) format('opentype');
  font-weight: normal;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fffcf9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    padding-left: 3%;
    padding-right: 3%;
    padding-bottom: 10%;
}

.fit
{
    width: 100%;
}